@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap);
*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}::selection{background:#5d0cff}body{overflow:hidden;font-size:14px;font-family:"Roboto Mono", monospace;background:linear-gradient(90deg, #3010ff 0%, #6473ff 65%)}#root{width:100vw;min-height:100vh;display:grid;place-content:center}.todo-app{width:40vw;min-width:500px;min-height:280px;height:auto;background:#161a2b;border-radius:4px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;position:relative}.todo-app .popup{background:linear-gradient(45deg, #9b00fa 0%, #5d0cff 100%);position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:80%;height:10rem;color:#fff;font-size:1.6rem;padding:1rem;box-shadow:0 0 25px #5d0cff}.todo-app .popup-text{text-align:center;margin-top:0.5rem;font-size:1.5rem}.todo-app .popup-button{font-family:"Roboto Mono", monospace;padding:0.7rem 0.6rem;border-radius:0 4px 4px 0;border:none;outline:none;cursor:pointer;color:#fff;width:5rem;margin:0 auto;margin-top:1rem;border-radius:50px;text-align:center;font-size:1rem;font-weight:500;color:#9b00fa}.todo-app h2{text-align:center;color:#fff;margin:1rem 0}.todo-app form{width:75%;display:-webkit-flex;display:flex;border-radius:5px;border:#5d0cff;margin-bottom:1rem}.todo-app form .todo-input{font-family:"Roboto Mono", monospace;width:75%;font-size:1rem;border-radius:4px 0 0 4px;color:#fff;outline:none;padding:0.7rem 0.3rem;border:2px solid #5d0cff;background-color:#161a2b;text-indent:12px;caret-color:#5d0cff}.todo-app form .todo-input:focus{box-shadow:0 0 6px #5d0cff}.todo-app form .todo-input::-webkit-input-placeholder{color:#fff;opacity:80%}.todo-app form .todo-input:-ms-input-placeholder{color:#fff;opacity:80%}.todo-app form .todo-input::placeholder{color:#fff;opacity:80%}.todo-app form .todo-button{font-family:"Roboto Mono", monospace;padding:0.7rem 0.6rem;border-radius:0 4px 4px 0;border:none;outline:none;cursor:pointer;color:#fff;background:linear-gradient(91deg, #5d0cff 0%, #9b00fa 100%);text-align:center;-webkit-flex:1 1;flex:1 1;transition:0.2s ease-in}.todo-app form .todo-button:hover{background:linear-gradient(91deg, #5d0cff 0%, #5d0cff 100%);letter-spacing:1px}.todo-app form .todo-button:focus{box-shadow:0 0 8px #5d0cff}.todo-app .item-list-wrapper .scroll-wrapper{max-height:60vh;overflow-y:auto;overflow-x:hidden}.todo-app .item-list-wrapper .clearall{margin:1.5rem 0.8rem;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.todo-app .item-list-wrapper .clearall .clearall-button{outline:none;border:none;cursor:pointer;padding:0.7rem 0.8rem;border-radius:4px;font-family:"Roboto Mono", monospace;font-size:0.8rem;color:#fff;background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%)}.todo-app .intro{height:100%;width:100%;color:#fff;text-transform:uppercase;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:1.5rem}.todo-app .intro-text{border:2px solid #5d0cff;padding:1rem 2rem;transition:all 0.3s}.todo-app .intro-text:hover{padding:1.2rem 2.2rem}::-webkit-scrollbar{width:3px;background:linear-gradient(180deg, #5d0cff 0%, #9b00fa 100%)}::-webkit-scrollbar-thumb{background:#fff;border-radius:4px}

.item-list-wrapper{width:90%}.item-list-wrapper .todo-list{background:linear-gradient(91deg, #5d0cff 0%, #9b00fa 100%);margin:0.8rem;padding:0.7rem 0.4rem;border-radius:4px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.item-list-wrapper .todo-list:last-child{margin-bottom:0}.item-list-wrapper .todo-list:hover{background:linear-gradient(91deg, #3f37c9 0%, #7209b7 90%);color:#000}.item-list-wrapper .todo-list_text{width:100%;color:#fff;list-style:none;padding-left:0.5rem}.item-list-wrapper .todo-list_text::first-letter{text-transform:capitalize}.item-list-wrapper .todo-list svg{cursor:pointer;width:1.2rem;height:1.2rem;margin:0.2rem 0.5rem;fill:#fff;transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}.item-list-wrapper .todo-list svg:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.item-list-wrapper .todo-list svg.todo-list_delete{fill:red}

